*{
    box-sizing: border-box;
    margin: 0;
}

.app-container{
    background-color: black;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app-heading{
    color: rgb(168, 168, 168);
    z-index: 1000;
    position: absolute;
    left: 0;
    top: 0;
    padding: 0px 20px;
    font-style: italic;
    display: flex;
    align-items: center;
}

.app-heading img{
    height: 1em;
    width: 1em;
    margin: 0 3px;
}

.main-container{
    // background-color: #A5F1F6;
    background-color: #303030;
    width: 98vw;
    height: 99.5vh;
    border-radius: 5px;
    display: flex;
    color: beige;
}

.player{
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    height: 6em;
    z-index: 6;

    .player-container{
        
        align-items: center;
        width: 98vw;
        background-color: #2B3A69;
        display: grid;
        grid-template-columns: 6fr 7fr 6fr;

    }

    .song-info{
        display: flex;
        justify-content: flex-start;
    }

    .song-info-img img{
        height: 6em;
        width: 7em;
    }

    .song-info-title{
        margin: 10px;
        align-self: center;
        font-size: 18px;

        p{
            padding: 2px;
        }

        :last-child{
            font-weight: 700;
        }
    }

    .player-controls-buttons{
        display: flex;
        justify-content: center;
    }

    .player-controls-buttons > div{
        align-self: center;
    }
    
    .play{
        margin: 0 15px;
    }
    
    .play-button{
        border-radius: 99%;
        border: 2px solid #AB3C3C;
        height: 2.5em;
        width: 2.5em;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 22px;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        cursor: pointer;
    
            .faplay, .FaPauseCircle{
                color: #560C53;
            }
    
            .faplay{
                font-size: 1em;
            }
    
            .FaPauseCircle{
                font-size: 1.5em;
            }
    
    
    }
    
    .play-button:hover{
        font-size: 22px;
        background-color: #AB3C3C;
    }

    .next-button, .previous-button {
        font-size: 2.5em;
        cursor: pointer;
    }

    .shuffle-button, .repeat-button{
        font-size: 1.5em;
        cursor: pointer;
        margin: 15px;;
    }

    .player-controls-status-bar{
        margin:5px 0 ;
    }

    .play-progress-bar-div{
        display: flex;
        align-items: center;
        position: relative;
        justify-content: center;
        margin-left: 10px;
    }

    .play-progress-bar{
        //border: 3px solid beige;
        width: 350px;
        cursor: pointer;
    }

    .play-progress-bar::-webkit-slider-thumb {
        pointer-events:none;
    }

    .play-progress-bar-start-time{
        margin-left: -5px;
        margin-right: 5px;
    }

    .play-progress-bar-end-time{
        margin-left: 5px;
        margin-right: -5px;
    }

    .play-progress-pointer{
        height: 15px;
        width: 15px;
        border: 2px solid red;
        border-radius: 99%;
        position: absolute;
        left: 100px;
        background: red;
    }

    .volume-h-speaker-img{
        font-size: 18px;
        margin-right: 5px;
        cursor: pointer;
    }

    .btn-selected{
        background-color: #1b294e;
        padding: 2px;
        width: 2em;
        border-radius: 39%;
        box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
        text-align: center;
    }

    .other-actions{
        .volume-h-div{
            display: flex;
            align-items: center;

            span{
                padding: 0 5px;
                padding-bottom: 3px;
            }
        }
    }

}

.sidebar{
    width: 20vw;
    height: 85vh;
    background-color: #AB3C3C;
    border-radius: 5px;
}

.screen{
    width: 78vw;
    height: 85vh;
    text-align: left;
    display: block;

    .header{
        // padding: 10px 0 0px 0;
        margin-left: 10px;
    }

    .group-band{
        margin: 5px 10px;
        border-bottom: 1px solid grey;
        display: grid;
        grid-template-columns: 2fr 2fr 5fr 2fr 24fr;
        grid-column-gap: 20px;

        div{
            width: auto;
            cursor: pointer;
            padding: 5px 5px 0 5px;

            h3{
                font-weight: 500;
            }

            a{
                color: inherit;
                text-decoration: none;
            }
        }

        .group-band-highlight{
            border-bottom: 2px solid #dfbd16;
            border-radius: 2px;
        }
    }

    .album-list, .album-artist-album-list{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-row-gap: 15px;
        max-height: 72vh;
        overflow-y: auto;
        z-index: 1;
        position: relative;
        background: #303030;
        padding: 10px;

        .album-lKey{
            padding: 5px;
            padding-left: 2px;
            font-size: 18px;
            font-weight: 700;
            grid-column: span 6;
        }

        .album-artist-thumb{
            width: 10em;
        }

        .album-thumb-img-div{
            position: relative;
            img{
                height: 9em;
                width: 9em;
                cursor: pointer;
                border-radius: 20px;
                border-bottom-left-radius: unset;
                box-shadow: rgb(0 0 0 / 40%) 0px 2px 4px, rgb(0 0 0 / 30%) 0px 7px 13px -3px, rgb(0 0 0 / 20%) 0px -3px 0px inset;
            }
        }

        .album-thumb-img-div-link{
            position: absolute;
            height: 9em;
            width: 9em;
            top: 0;
            align-items: center;
            display: flex;
            justify-content: center;
            display: none;
            background-color: #eaf0f0;
            opacity: 0.2;
            color: #093fa3;
            cursor: pointer;

            svg{
                font-size: 2em;
                font-weight: 900;
            }
        }

        .album-thumb-img-div a:hover + .album-thumb-img-div-link{
            display: flex;
        }

        .album-thumb-details{
            padding-right: 5px;
            width: 10em;

            label{
                font-size: 15px;
            }

            p{
                margin-top: 5px;
                font-size: 13px;
            }
        }

    }

    .album-artist-album-list{
        max-height: 18em;
    }

    .artists-list{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-row-gap: 15px;
