                }
            }
        
            .track:hover{
                background-color: #4d4d4d;
                padding: 1px;
            }
        
            .text-highlighted-y{
                color: #dfbd16;
                font-weight: 500;

                a{
                    color: #dfbd16;
                    font-weight: 500; 
                }
            }
        }
    }

    .album-artist{
        padding: 10px 0 0 15px;    

        .album-artist-img-div img{
            height: 10em;
            width: 10em;
        }
        .album-artist-img-div-container{
            display: grid;
            grid-template-columns: 1fr 4fr;
        
            .album-artist-details{
                display: flex;
                flex-direction: column;
        
                h3{
                    margin-top: -5px;
                    font-weight: 500;
                    margin-bottom: 3px;
                }
        
                label{
                    font-size: 15px;
                    margin-bottom: 3px;
                }

                .album-artist-wiki-summary{
                    margin-top: 5px;
                    font-size: 14px;
                    width: 94%;
