  .spinner{
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    overflow: hidden;
    position: relative;
	animation: text_color 2s ease-in-out infinite;
    position: fixed;
    left: 45%;
    top: 35%;
    opacity: 0.3;
}

.spinner-sector{
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 15px solid transparent;
    mix-blend-mode: overlay;
    animation: rotate var(--duration) var(--timing) infinite;
}

.spinner-sector-red{
    border-top-color: lightcoral;
    --duration:1.5s;
    --timing:ease-in-out;
}

.spinner-sector-blue{
    border-left-color: lightblue;
    --duration:2s;
    --timing:ease-in;
}

.spinner-sector-green{
    border-right-color: lightgreen;
    --duration:2.5s;
    --timing:ease-out;
}

@keyframes rotate{
    0%{
        transform: rotate(0);
    }

    100%{
        transform: rotate(360deg);
    }
}

@keyframes text_color{
    0%,100%{
        //color: rgba(0, 0, 0, 1);
        color: beige;
    }
    25%,75%{
       // color: rgba(0, 0, 0, .5);
       color: rgb(117, 117, 107);
    }
    50%{
       // color: rgba(0, 0, 0, .1);
       color: rgb(199, 199, 182);
    }
}